* {
    box-sizing: border-box;
    font-family: "Lato", sans-serif;
    font-weight: normal;
    background: none;
    border: none;
    letter-spacing: 0.4px;
}
html,
body {
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    line-height: 1.5;
    color: #212529;
    background-color: #fff;
    overflow: none;
}

h1,
h2,
h3,
h4 {
    line-height: 1.2;
}

p {
    width: 100%;
}

a {
    text-decoration: none;
    color: $purple;
}
